import React from "react";
import { Helmet } from "react-helmet";

import Button from "../../components/button/Button";

import "./anyPage.scss";
import imageWebp from "../../assets/images/product-page-error/error_img.webp";
import imageSrc from "../../assets/images/product-page-error/error_img.jpg";

const AnyPage: React.FC = () => {
	return (
		<>
			<Helmet>
				<title>Неіснуюча сторінка - Просто паста</title>
				<meta
					name="description"
					content="На сторінці можна відшукати пасту на будь який Ваш смак. Арахісова солодка, солона, з фініками, з молоком, шоколадна. Великий та малий розмір. Можна додати додаткову начинку"
				/>
			</Helmet>
			<div className="page-anypage">
				<div className="container">
					<div className="page-anypage__inner">
						<picture className="page-anypage__image">
							<source srcSet={imageWebp} type="image/webp" />
							<img src={imageSrc} alt="Сталася помилка" />
						</picture>
						<div className="page-anypage__content">
							<p className="page-anypage__title">
								Це <span>не</span> помилка!
							</p>
							<p className="page-anypage__subtitle">
								Просто, ще не всі сторінки для цього сайту розроблено. <br />
								Загляньте до нас дещо згодом - можливо, потрібна Вам сторінка з'явиться :)
							</p>
							<Button className="page-anypage__button" href="/" title="На головну" />
						</div>
					</div>
				</div>
			</div>
		</>
	);
};

export default AnyPage;
